<template>
	<div class="operation-product" @click.stop="pClick">
		<btn :title="'-'" class="btnleft" @btnClick="cut" v-show="isShow"/>
		<span v-show="isShow" class="show-num">{{num}}</span>
		<btn :title="'+'" class="btnright" @btnClick="add"/>
	</div>
</template>

<script>
import Btn from './Btn'

export default {
  name: 'OperationProduct',
  components:{
	Btn
  },
  props:{
	productObj:{
		type:Object,
		default(){
			return{}
		}
	}
  },
  data(){
	return{
		num:0
	}
  },
  methods:{
	add(){
		//点击+号 找到对应的存在数组里的对象-如果该对象存在在state数组里面的话就显示该数字
		this.num++
		//使用vuex管理添加
		this.$store.dispatch("addOrderProduct",this.productObj).then(res=>{
			this.$toast.show(res,3000)
		})
	},
	cut(){
		//减少
		if(this.num<=0)return 
		this.num--
		this.$store.dispatch("cutOrderProduct",this.productObj).then(res=>{
			this.$toast.show(res,3000)
		})
	},
	pClick(){
	
	}
  },
  computed:{
	//这个productObj是商品的并不是vuex里面数组的 所以要获取数量就必须获取state里面orderProductList数组的
	isShow(){
		return this.num
	},
	//数量
  },
  created(){
	//console.log(this.productObj.id)
  }

}
</script>

<style>

.operation-product{
	text-align:right;
}
.btnleft{
	margin-right:25px;
	background:#ccc;
}
.show-num{
	font-size:18px;
}
.btnright{
	margin-left:25px;
}
</style>
